@font-face {
  font-family: StarJedai;
  src: url("./fonts/Starjedi.ttf");
}

@font-face {
  font-family: OpenSans;
  src: url("./fonts/OpenSans-Regular.ttf");
}


* {
  margin: 0;
  font-family: OpenSans, serif;
  font-size: 12px;
}

.title {
  color: rgb(189, 189, 2);
  font-family: StarJedai, serif;
  font-size: 50px;
}

.input-filter, .select-filter {
  background-color: white;
  border-radius: 15px;
  padding: 1px 5px;
}

.input-filter {
  width: 100px;
}

.input-planet {
  border-radius: 15px;
  padding: 5px 20px;
  width: 200px;
}

.container-filters-main {
  align-items: center;
  background-color: black;
  display: flex;
  flex-direction: column;
}

.container-filters {
  display: flex;
  justify-content: space-around;
  margin: 10px 0;
  width: 100%;
}

.header {
  align-items: center;
  background-color: black;
  display: flex;
  justify-content: space-around;
  padding: 10px;
}

.table-info {
  background-image: url('darthVaden.jpg');
  height: 488px;
}

.fieldset-filter {
  border: solid rgb(189, 189, 2) 1.5px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  padding: 0 0 8px 0;
  width: 45%;
}

.container-inputs {
  display: flex;
  justify-content: space-around;
}

.container-list-filters {
  align-items: center;
  color: rgb(189, 189, 2);
  display: flex;
  margin: 5px 40px;
}

label, legend {
  color: rgb(189, 189, 2);
}

th {
  color: rgb(189, 189, 2);
  font-size: 16px;
  padding: 5px 20px;
  text-align: center;
  width: 80px;
}

td {
  font-size: 15px;
  padding: 5px 20px;
  text-align: center;
  width: 80px;
}

.btn-delete-filter {
  background-color: black;
  border: none;
  color: red;
  font-size: 20px;
  font-weight: 900;
  margin-left: 10px;
}

.fieldset-sort {
  align-items: center;
  border: solid rgb(189, 189, 2) 1.5px;
  border-radius: 5px;
  display: flex;
  height: 40px;
  justify-content: space-around;
  text-align: center;
  padding: 0 0 8px 0;
  width: 45%;
}

.input-radio {
  margin-right: 10px;
}

.btn-sort, .btn-filter {
  background-color: rgb(189, 189, 2);
  border-style: none;
  border-radius: 5px;
  height: 24px;
  padding: 0 10px;
  font-weight: 600;
  width: 60px;
}

button {
  cursor: pointer;
}

.input-value {
  width: 110px;
  height: 15px;
}

.link-info {
  color: blue;
  cursor: pointer;
  font-size: 25px;
  font-weight: bold;
  text-decoration: none;
}

.container-footer {
  align-items: center;
  display: flex;
  justify-content: center;
  background-color: black;
  height: 70px;
}

.footer-company {
  color: rgb(189, 189, 2);
}

.cell-title {
  background-color: #282c34;
  display: flex;
  justify-content: space-between;
}

.cell-info {
  align-items: center;
  border-bottom: solid 0.1px rgb(107, 106, 106);
  display: flex;
  justify-content: space-between;
}

.container-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.container-description {
  align-items: center;
  display: flex;
  justify-content: space-around;
  margin: 20px 0;
}

.container-info {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 50%;
}

.title-details {
  color: rgb(189, 189, 2);
  font-family: StarJedai, serif;
  font-size: 35px;
  letter-spacing: 2px;
  margin-bottom: 20px;
  text-align: center;
}

.text-description {
  font-size: 18px;
}

.imgPlanet-details {
  border-radius: 5px;
  width: 30%;
}

.statistic-planet {
  display: flex;
  flex-direction: column;
  padding: 0 69px;
}

.lineInfo {
  display: flex;
}

.title-statistic {
  background-color: rgb(199, 196, 196);
  font-weight: 600;
  width: 160px;
}

.info-statistic {
  background-color: rgb(235, 232, 232);
  width: 100%;
}

.title-statistic, .info-statistic {
  border: solid 1px White;
  font-size: 15px;
  padding: 5px 20px;
}

.container-residents {
  margin-top: 20px;
}

.container-cards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 0 69px;
}

.container-resident-card {
  display: flex;
  margin-bottom: 20px;
}

.container-resident-info {
  display: flex;
  flex-direction: column;
  padding: 10px;
  width: 280px;
}

.imgResident {
  width: 150px;
  border-radius: 10px;
}

.nameResident {
  color: rgb(189, 189, 2);
  font-family: StarJedai, serif;
  font-size: 20px;
  letter-spacing: 2px;
  margin-bottom: 20px;
  text-align: center;
}

.container-films {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}

.container-img-films {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.imgFilm {
  border-radius: 9px;
  height: 300px;
  margin: 10px;
}